  -webkit-border-radius: 5px;
  border-radius: 5px;
  width: 70%;
  height: 450px;
  margin: 0 auto;
  background-color: #fff;
}

.auth-box a {
  color: inherit;
}

.auth-box a:hover, .auth-box a:focus {
  text-decoration: underline;
}

.auth-box:before {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: inline;
  content: ' ';
  vertical-align: middle;
  height: 100%;
}

.auth-box .content {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: inline;
  width: 99%;
  vertical-align: middle;
}

.auth-box .left {
  float: left;
  width: 40%;
  height: 100%;
  padding: 0 30px;
  text-align: center;
}

.auth-box .left:before {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: inline;
  content: ' ';
  vertical-align: middle;
  height: 100%;
}

.auth-box .right {
  float: right;
  width: 60%;
  height: 100%;
  position: relative;
  background-image: url("../img/login-bg.jpg?1479712029");
  background-repeat: no-repeat;
  background-size: cover;
}

.auth-box .right:before {
  display: inline-block;
  vertical-align: middle;
  *vertical-align: auto;
  *zoom: 1;
  *display: inline;
  content: ' ';
  vertical-align: middle;
  height: 100%;
}

.auth-box .right .text {
