html {
  height: 100%;
}

html .login-page {
  height: 100%;
}

html .login-page #login-background {
  height: 100%;
}

html .login-page #login-background .animation-pulseSlow {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  margin-left: 0;
}

html .login-page .logo-img {
  position: absolute;
  top: 70px;
  left: 50%;
  -webkit-transform: translate(-50%);
  transform: translate(-50%);
  z-index: 2;
  width: 70px;
}

html .login-page .animation-fadeIn .login-title {
  background: rgba(85, 92, 95, 0.9) !important;
}

html .login-page .animation-fadeIn .login-title h1 {
  font-weight: 300;
}

html .login-page .animation-fadeIn .remember-me {
  position: absolute;
  left: 80px;
  top: 5px;
  width: 125px;
}

@media (max-width: 767px) {
  html .login-page .animation-fadeIn .remember-me {
    display: none;
  }
}

html #login-background {
  background-image: url("/img/backgrounds/login-background.jpg");
  background-size: cover;
  background-position: center center;
}

